body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  background-color: #030035;
}

p, legend {
  color: #FFFFFF;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
  color: #98CA3F;
  font-weight: 900;
}

h2 {
  font-size: 3.4rem;
  color: #FFFFFF;
}

h3 {
  font-size: 3rem;
  color: #FFFFFF;
}

h4 {
  font-size: 2.6rem;
  color: #FFFFFF;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.boton-verde {
  background-color: #98CA3F;
  color: #FFFFFF;
  text-decoration: none;
  font-weight: 700;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  border-radius: 1rem;
  display: block;
}
.boton-verde:hover {
  background-color: #8bbc34;
  cursor: pointer;
}

.boton-contacto {
  background-color: #98CA3F;
  color: #FFFFFF;
  text-decoration: none;
  font-weight: 700;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  border-radius: 1rem;
  display: inline-block;
  margin-bottom: 2rem;
}
.boton-contacto:hover {
  background-color: #8bbc34;
  cursor: pointer;
}

.boton-rojo {
  background-color: rgb(179, 6, 6);
  color: #FFFFFF;
  text-decoration: none;
  font-weight: 700;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  border-radius: 1rem;
  display: block;
  width: 100%;
}
.boton-rojo:hover {
  background-color: #9a0505;
  cursor: pointer;
}

.contenedor-header {
  background-color: #072F76;
  align-items: center;
}
@media (min-width: 768px) {
  .contenedor-header {
    height: auto;
    padding: 1rem;
  }
}

.barra {
  display: flex;
  flex-direction: column;
  padding: 1rem 0;
}
.barra .menu-movil {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0 2rem;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 0;
  }
}

.divlog a {
  display: flex;
  justify-content: center;
}

.logo {
  width: auto;
  height: 10rem;
}
@media (min-width: 768px) {
  .logo {
    height: 10rem;
    align-items: center;
  }
}

.navegacion, .login {
  display: none;
}
@media (min-width: 768px) {
  .navegacion, .login {
    display: flex;
    align-items: center;
  }
}
.navegacion a, .login a {
  text-decoration: none;
  text-align: center;
  color: #FFFFFF;
  display: block;
  padding: 0.5rem;
  background-color: #98CA3F;
  border-radius: 0.5rem;
  font-weight: 700;
  margin: 1rem 2rem 0;
}
@media (min-width: 768px) {
  .navegacion a, .login a {
    margin: 0 2rem 0 0;
  }
  .navegacion a:last-of-type, .login a:last-of-type {
    margin-right: 0;
  }
}

.footer {
  background-color: #072F76;
  padding: 2rem;
}

.copyrithing {
  color: #FFFFFF;
  text-align: center;
}

@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  height: 5rem;
  align-items: center;
}

@media (max-width: 480px) and (max-width: 768px) {
  .mostrar {
    display: block;
  }
}
@media (min-width: 480px) and (max-width: 768px) {
  .mostrar {
    display: block;
  }
}

.portada {
  background-image: url(../../../build/img/2.png);
  background-image: url(../../../build/img/2.webp);
  background-position: center center;
  background-size: cover;
}
.portada h1 {
  margin: 0;
  padding-top: 3rem;
}
.portada p {
  color: #FFFFFF;
  margin-left: 2rem;
}
.portada a {
  color: #98CA3F;
  margin-left: 2rem;
}
@media (min-width: 768px) {
  .portada {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
    height: 40rem;
  }
}

.imagen-contacto {
  background-image: url(../../../build/img/4.png);
  background-image: url(../../../build/img/4.webp);
  background-position: center center;
  background-size: cover;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .imagen-contacto {
    padding: 10rem 0;
  }
}
.imagen-contacto p {
  color: #FFFFFF;
  text-align: center;
}
.imagen-contacto h2 {
  color: #FFFFFF;
  font-weight: 900;
}

@media (min-width: 768px) {
  .contenedor-anuncio {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.anuncio {
  border-style: double;
  border-color: #98CA3F;
  border-radius: 2rem;
  border-width: 5px;
}
.anuncio .contenedor-anuncio {
  padding: 2rem;
}
.anuncio h3 {
  margin: 0;
  color: #98CA3F;
  font-weight: 700;
}
.anuncio p {
  margin: 0;
  color: #FFFFFF;
  padding: 2rem;
}
.anuncio picture {
  background-color: #FFFFFF;
  border-radius: 2rem;
  margin: 1rem;
  width: auto;
  display: flex;
  align-items: center;
}
.anuncio img {
  border-radius: 2rem;
  display: block;
  justify-content: center;
}
.anuncio .enlace {
  padding: 1rem 2rem;
}

.seccion h2 {
  color: #98CA3F;
  font-weight: 700;
}

.testimonio h2 {
  color: #98CA3F;
  font-weight: 900;
}
.testimonio h3, .testimonio p {
  color: #FFFFFF;
}
.testimonio p {
  margin-right: 1rem;
}
.testimonio .contenerdor-testimonios {
  border: solid;
  margin-bottom: 2.5rem;
  border-radius: 2rem;
  border-color: #98CA3F;
}
.testimonio .contenerdor-testimonios .imagen-testiminio {
  padding: 2rem;
}
@media (min-width: 768px) {
  .testimonio .contenerdor-testimonios .imagen-testiminio {
    height: 30rem;
    display: flex;
    align-items: center;
  }
}
.testimonio .contenerdor-testimonios img {
  height: 100%;
  width: cover;
  border-radius: 2rem;
}
@media (min-width: 768px) {
  .testimonio .contenerdor-testimonios img {
    margin-left: 5rem;
    margin-right: 5rem;
  }
}
@media (min-width: 768px) {
  .testimonio .contenerdor-testimonios {
    display: grid;
    gap: 4rem;
    grid-template-columns: 1fr 2fr;
  }
}

.tienda div {
  margin-bottom: 2rem;
}

.cotizador {
  border-style: double;
  border-color: #98CA3F;
  border-radius: 2rem;
  border-width: 5px;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .cotizador {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (min-width: 768px) {
  .base-delantal {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.select {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 1rem;
}

input:not([type=submit]),
textarea,
select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #98CA3F;
  border: 1px solid #1a1b15;
  border-radius: 2rem;
  color: #FFFFFF;
  font-weight: 700;
  margin-bottom: 1rem;
}

textarea {
  display: flex;
  flex-direction: column;
  height: 20rem;
  align-items: center;
}

label {
  color: #FFFFFF;
  font-weight: 700;
}

.picture {
  background-color: #FFFFFF;
  border-radius: 2rem;
  margin: 1rem;
  width: auto;
  display: flex;
  align-items: center;
}
.picture img {
  border-radius: 2rem;
}

.contenedor-formulario {
  border-style: double;
  border-color: #98CA3F;
  border-radius: 2rem;
  border-width: 5px;
  margin-bottom: 3rem;
}

.alerta {
  padding: 0.5rem;
  text-align: center;
  color: #FFFFFF;
  font-weight: 700;
  text-transform: uppercase;
  margin: 1rem 0;
}
.alerta.error {
  background-color: rgb(179, 6, 6);
}

table.modelos {
  margin-top: 4rem;
  margin-bottom: 4rem;
  width: 100%;
  border-spacing: 0;
  color: #FFFFFF;
  text-align: center;
}
table.modelos thead {
  background-color: #98CA3F;
}
table.modelos thead th {
  color: #FFFFFF;
  padding: 2rem;
}
table.modelos tbody img {
  background-color: #FFFFFF;
  border-radius: 0.5rem;
}
table.modelos tbody tr {
  background-color: #072F76;
  border-radius: 2rem;
}

.imagen-tabla {
  width: 10rem;
}

@media (max-width: 768px) {
  .hiden {
    display: none;
  }
}